.product {
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #ccc;
  color: rgb(182, 6, 135);
}

.product h3 {
  margin: 0 0 0.25rem 0;
}

.description {
  font-style: italic;
}

.price {
  margin-top: 0.25rem;
  font-weight: bold;
  color: #70035a;
  font-size: 1.25rem;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.img {
  height: 120px;
  width: 120px;
}
